<template>
  <v-dialog  full-width v-model="dialog">
    <v-btn flat slot="activator" class="success">Add New Project</v-btn>
    <v-card>
      <v-card-title>
        <h1>Add a New Project</h1>
      </v-card-title>
      <v-card-text>
        <v-form class="px-1" ref="form">
          <v-text-field v-model="project_id" label="Project_id" prepend-icon="edit" ></v-text-field>
          <v-text-field v-model="title" label="Title" prepend-icon="edit" ></v-text-field>
          <v-textarea v-model="objective" label="Objective" prepend-icon="edit" ></v-textarea>
          <v-textarea v-model="approach" label="Approach" prepend-icon="edit" ></v-textarea>
          <v-text-field v-model="algorithms" label="Algorithms" prepend-icon="edit" ></v-text-field>
          <v-text-field v-model="person" label="Person" prepend-icon="edit" ></v-text-field>
          <v-text-field v-model="status" label="Status" prepend-icon="edit" ></v-text-field>

          <v-menu v-model="menu" :close-on-content-click="false">
            <v-text-field slot="activator" :value="formattedDate" clearable label="Due date" prepend-icon="date_range">
            </v-text-field>
            <v-date-picker v-model="due" @change="menu = false"></v-date-picker>
          </v-menu>

          <v-spacer></v-spacer>

          <v-btn flat @click="submit" class="success mx-5 mt-3" :loading="loading">Add Project</v-btn>
          <v-btn flat @click="clear" class="warning mx-5 mt-3" :loading="loading" >Reset Form</v-btn>
          
        </v-form>
      </v-card-text>
    </v-card>
  </v-dialog>
  
</template>

<script>
import format from 'date-fns/format'
import db from '@/fb'

export default {
  data() {
    return {
      Project_id: '',
      title: '',
      Objective: '',
      Approach: '',
      Algorithms: '',
      person: '',
      Status: '',
      due: null,
      menu: false,
      inputRules: [
        v => !!v || 'This field is required',
        v => v.length >= 3 || 'Minimum length is 3 characters'
      ],
      loading: false,
      dialog: false
    }
  },
  methods: {
    submit() {
      if(this.$refs.form.validate()) {
          this.loading = true
          const project = { 
          project_id: this.project_id,
          title: this.title,
          objective: this.objective,
          approach: this.approach,
          algorithms: this.algorithms,
          person: this.person,
          status: this.status,
          due: format(this.due, 'Do MMM YYYY'),
          
        }
        db.collection('projects').add(project).then(() => {
          this.loading = false
          this.dialog = false
          this.$emit('projectAdded')
        })
      }
    }, clear(){this.$refs.form.reset() }
  },
  computed: {
    formattedDate () {
      console.log(this.due)
      return this.due ? format(this.due, 'Do MMM YYYY') : ''
    }
  }
}
</script>
