<template>
  <div class="projects">
    <h1 class="heading grey--text">Projects</h1>

    <v-container class="my-1">
      <v-expansion-panel focusable  inset>
        <v-expansion-panel-content v-for="project in myProjects" :key="project.title">
          <div slot="header" class="py-1">{{ project.project_id +'  '+ project.title }}</div>
          
          <v-card>
            <v-card-text class="px-4">
             <h4>Objective of usecase</h4>
             <div>{{ project.objective }}</div>
            <br>
            <h4>Solution Approach</h4>
            <div>{{ project.approach }}</div>
            <br>
            <h4>Algorithms used in solution</h4>
            <div>{{ project.algorithms }}</div>
            <br>
            <h4>Project Status</h4>
            <div>{{ project.status }}</div>
            <br>
            <h4>Due date</h4>
            <div>{{ project.due }}</div>
            </v-card-text>
            

          </v-card>
        </v-expansion-panel-content>
      </v-expansion-panel>
    </v-container>
    
  </div>
</template>

<script>
import db from '@/fb'

export default {
  data() {
    return {
      projects: []
    }
  },
  computed: {
    myProjects() {
      return this.projects.filter(project => {
        return project.person != 'test' 
      })
    }
  },
  created() {
    db.collection('projects').onSnapshot(res => {
      const changes = res.docChanges();

      changes.forEach(change => {
        if (change.type === 'added') {
          this.projects.push({
            ...change.doc.data(),
            id: change.doc.id
          })
        }  
      })
    })
  }
}
</script>
