<template>
  <div class="dashboard">
    <h1 class="heading grey--text">Dashboard</h1>

    <v-container class="my-1">
   
    <v-layout row wrap>
        <v-flex xs12 sm6 md3 lg3 v-for="person in kpi" :key="person.name">
          <v-card height =160px class="text-xs-center ma-5" color="blue-grey lighten-4">
            <v-responsive class="pt-3">
              <v-avatar size="50" class="blue-grey lighten-4">
                <img :src="person.avatar">
              </v-avatar>
            </v-responsive>
            <v-card-text>
              <div class="subheading">{{ person.name }}</div>
              <div class="black--text">{{ person.role }}</div>
            </v-card-text>
            <v-card-actions>
            
            </v-card-actions>
          </v-card>
        </v-flex>
      </v-layout>


      <v-layout row justify-start class="mb-3">
        <v-tooltip top>
          <v-btn small flat color="grey" @click="sortBy('project_id')" slot="activator">
            <v-icon small left>folder</v-icon>
            <span class="caption text-lowercase">By project</span>
          </v-btn>
          <span>Sort by project #</span>
        </v-tooltip>
        <v-tooltip top>
          <v-btn small flat color="grey" @click="sortBy('person')" slot="activator">
            <v-icon small left>person</v-icon>
            <span class="caption text-lowercase">By Person</span>
          </v-btn>
          <span>Sort by Person</span>
        </v-tooltip>
      </v-layout>
      
      <v-card flat v-for="project in projects" :key="project.title">
        <v-layout row wrap :class="`pa-3 project ${project.status}`">
          <v-flex xs12 sm2>
            <div class="caption grey--text">Project ID</div>
            <div>{{ project.project_id }}</div>
          </v-flex>

           <v-flex xs6  md6>
            <div class="caption grey--text">Project title</div>
            <div>{{ project.title }}</div>
          </v-flex>

          
          <v-flex xs6 sm4 md2>
            <div class="caption grey--text">Person</div>
            <div>{{ project.person }}</div>
          </v-flex>
          
          <v-flex xs2 sm4 md2>
            <div class="right">
              <v-chip small :class="`${project.status} white--text my-2 caption`">{{ project.status }}</v-chip>
            </div>
          </v-flex>

        

        </v-layout>
        <v-divider></v-divider>
      </v-card>

    </v-container>
   
  </div>
</template>

<script>
import db from '@/fb'

export default {
  data() {
    return {
      projects: [],
      kpi: [
        { name: '# complete', role: '30', avatar: '/KPI-1.png' },
        { name: '# ongoing', role: '7', avatar: '/KPI-2.png' },
        { name: '# overdue', role: '3', avatar: '/KPI-3.png' },
        { name: '# total', role: '40', avatar: '/KPI-4.png' }]
    }
  },
  methods: {
    sortBy(prop) {
      this.projects.sort((a,b) => a[prop] < b[prop] ? -1 : 1)
    }
  },
  created() {
    db.collection('projects').onSnapshot(res => {
      const changes = res.docChanges();

      changes.forEach(change => {
        if (change.type === 'added') {
          this.projects.push({
            ...change.doc.data(),
            id: change.doc.id,
            project_id: change.doc.data().project_id
          })
        }  
      })
    })
  }
}
</script>

<style>

.project.complete{
  border-left: 4px solid #3cd1c2;
}
.project.ongoing{
  border-left: 4px solid #ffaa2c;
}
.project.overdue{
  border-left: 4px solid #f83e70;
}
.v-chip.complete{
  background: #3cd1c2;
}
.v-chip.ongoing{
  background: #ffaa2c;
}
.v-chip.overdue{
  background: #f83e70;
}

</style>
